<template>
  <v-app>
    <v-row align="center" justify="center">
      <v-col cols="12" sm="8" md="4">
        <v-card width="500" class="elevation-5">
          <v-container>
          <br />
          <div style="text-align: center;">
            <h3>Login</h3>
          </div>
          <br />
          <v-divider></v-divider>
          <br />
          <v-card-text>
            <v-form ref="form" v-model="valid" :lazy-validation="lazy">
              <v-text-field
                label=" Email"
                prepend-inner-icon="mdi-email"
                v-model="email"
                outlined
                :rules="emailRules"
                required
              />
              <v-text-field
                v-model="pass"
                :type="showPassword ? 'text' : 'password'"
                label=" Password"
                prepend-inner-icon="mdi-lock"
                outlined
                :rules="[v => !!v || 'Password est obligatoire']"
                :append-icon="showPassword ? 'mdi-eye' : 'mdi-eye-off'"
                @click:append="showPassword = !showPassword"
                required
              />
            </v-form>

            <v-btn color="info" class="mr-4" @click="validate" style="height:45px;" block>
              <v-icon left>mdi-login</v-icon>Login
            </v-btn>
            <br>
            <v-btn color="info" class="mr-4" @click="validate" style="height:45px;" to="/" block>
              <v-icon left>mdi-login</v-icon>Cancel
            </v-btn>
            <br />
          </v-card-text>
          </v-container>
        </v-card>
      </v-col>
    </v-row>
  </v-app>
</template>
<script>

import Axios from 'axios';

export default {
  name: "HelloWorld",
  data() {
    return {
      email: "",
      pass: "",
      emailRules: [
        (v) => !!v || "E-mail est obligatoire",
        (v) => /.+@.+\..+/.test(v) || "E-mail incorrect",
      ],
      lazy: false,
      valid: true,
      showPassword: false,
      users: [],
      v: false,
      role: ""
    };
  },
  methods: {
    validate( ) {
      if (this.$refs.form.validate())
      {
        Axios.get(
                "http://localhost:3000/user"
              ).then((res) => {
                res.data.forEach((item) => {
                if (this.email == item.login && this.pass == item.mdp)  {
                  // this.$router.push({ path: "/student" });
                  localStorage.setItem("id", item.id);
                  localStorage.setItem("login",  item.login);
                  localStorage.setItem("role",  item.role);
                  localStorage.setItem("nom",  item.Nom);
                  this.role = item.role;
                  // v means user exists
                  this.v=true;
                  }
                })
                if(this.v){
                  if(this.role == "student"){
                 // this.$router.push({ path: "/student" });
                 this.$router.push({ path: "/Nav2" });
                  } else{
                  this.$router.push({ path: "/Nav" });
                  }
                }else {
                  alert("WRONG PSW OR LOGIN CHECK")
                }
                // this.users = res.data;
              });
      }},
        reset() {
      this.$refs.form.reset();
    },
  },
};
</script>